<template>
  <div id="app">
  	<v-header :seller="seller"></v-header>
    <div class="tab border-1px">
      <router-link tag="div" class="tab-item" to="/goods">
        <span>商品</span>
      </router-link>
      <router-link tag="div" class="tab-item" to="/rating">
        <span>评论</span>
      </router-link>
      <router-link tag="div" class="tab-item" to="/seller">
        <span>商家</span>
      </router-link>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import header from './components/header/header'

const ERR_OK = 0

export default {
  data () {
    return {
     seller: {}
    }
  },
  created () {
    this.$http.get('/api/seller').then(
      (res) => {
        res = res.body
        if (res.errno === ERR_OK) {
          this.seller = res.data
        }
      })
  },
  components: {
    'v-header': header
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">

.tab{
  display: flex;
  width: 100%;
  line-height: 40px;
  position:relative;
}
.tab:after{
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top:1px solid rgba(7,17,27,0.1);
  content: ' ';
}



.tab .tab-item{
  flex:auto;
  text-align :center;
}

.tab .tab-item span{
  font-size: 14px;
  color: rgb(11,85,93);
}
.tab .router-link-active span{
  color: rgb(240,20,20);
}
</style>



