<template>
  <div id="goods">
    <div class="menu-wrapper" v-el:menu-wrapper>
      <ul>
        <li v-for="(item,i) in goods" class="menu-item">
          <span class="text">
            <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>

    <div class="foods-wrapper" v-el:foods-wrapper>
      <ul>
        <li v-for="(item,i) in goods" class="food-list">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="food-item">
              <div class="icon">
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'

const ERR_OK = 0

export default {
  props: {
    seller: {
      type: Object
    }
  },
  data () {
    return {
     goods: {}
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
    this.$http.get('/api/goods').then(
      (res) => {
        res = res.body
        if (res.errno === ERR_OK) {
          this.goods = res.data
          this.$nestTick(() => {
            this._initScroll()
          })
        }
      })
  },
  methods: {
    _initScroll () {
      this.meunScroll = new BScroll(this.$els.menuWrapper, {})
      this.foodsScroll = new BScroll(this.$els.foodsWrapper, {})
    }
  }
}
</script>

<style>
#goods{
  display: flex;
  position: absolute;
  top: 174px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
#goods .menu-wrapper{
  flex: 0 0 80px;
  width: 80px;
  background: #f3f5f7;
}
.menu-wrapper .menu-item{
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  padding: 0 12px;
}
.menu-item .icon{
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.menu-item .text{
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  font-size: 12px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.text .decrease{
  background-image: url("decrease_3@2x.png");
}
.text .discount{
  background-image: url("discount_3@2x.png");
}
.text .special{
  background-image: url("special_3@2x.png");
}
.text .invoice{
  background-image: url("invoice_3@2x.png");
}
.text .guarantee{
  background-image: url("guarantee_3@2x.png");
}

#goods .foods-wrapper{
  flex: 1;
}
.foods-wrapper .title{
  padding-left: 14px;
  height: 26px;
  line-height: 26px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  color: rgb(147,153,159);
  background: #f3f5f7;
}
.foods-wrapper .food-item{
  display: flex;
  margin: 18px;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.food-item:last-child{
  border-bottom: 0;
}
.food-item .icon{
  flex: 0 0 57px;
  margin-right: 10px;
}
.food-item .content{
  flex: 1;
}
.content .name{
  font-size: 14px;
  margin: 2px 0 8px 0;
  height: 14px;
  line-height: 14px;
  color: rgb(7,17,17);
}
.content .desc,.extra{
  line-height: 10px;
  font-size: 10px;
  color: rgb(147,153,159);
}
.content .desc{
  margin-bottom: 8px;
}
.content .extra .count{
  margin-right: 12px;
}
.content .price{
  font-size: 700;
  line-height: 24px;
}
.content .price .now{
  margin=right: 8px;
  font-size: 14px;
  color: rgb(240, 20, 20);
}
.content .price .old{
  text-decoration: line-through;
  font-size: 10px;
  color: rgb(147,153,159);
}

@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
.text .decrease{
  background-image: url("decrease_3@3x.png");
}
.text .discount{
  background-image: url("discount_3@3x.png");
}
.text .special{
  background-image: url("special_3@3x.png");
}
.text .invoice{
  background-image: url("invoice_3@3x.png");
}
.text .guarantee{
  background-image: url("guarantee_3@3x.png");
}
}
</style>


