<template>
  <div id="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar"/>
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>

        <div class="descreption">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>

        <div v-if="seller.supports" class="supports">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
        <div v-if="seller.supports" class="support-count" @click="showDetail">
          <span class="count">{{seller.supports.length}}个</span>
          <i class="icon-keyboard_arrow_right"></i>
        </div>
      </div>
    </div>
    <div class="bulletin-wrapper" @click="">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" width="100%" height="100%">
    </div>
    <div class="detail" v-show="detailShow" transition="fade">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wrapper">
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul v-if="seller.supports" class="supports">
            <li class="support-item" v-for="(item,i) in seller.supports">
              <span class="icon" :class="classMap[seller.supports[i].type]"></span>
              <span class="text">{{seller.supports[i].description}}</span>
            </li>
          </ul>
          <div class="title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin">
            <p calss="content">{{seller.bulletin}}</p>
          </div>
          
        </div>
      </div>
      <div class="detail-close" @click="hideDetail">
        <i class="icon-close"></i>
      </div>
    </div>
  </div>
</template>

<script>
import star from '../star/star'
export default {
  data () {
    return {
      detailShow: false
    }
  },
  methods: {
    showDetail () {
      this.detailShow = true
    },
    hideDetail () {
      this.detailShow = false
    }
  },
  props: {
    seller: {
      type: Object
    }
  },
  created () {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
  },
  components: {
    star
  }
}
</script>

<style scope>
#header{
  color:#fff;
  position: relative;
  background: rgba(7,17,27,0.5);
  overflow: hidden;
}
.content-wrapper{
  position:relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.avatar{
  display:inline-block;
  vertical-align: top;
}
.avatar img{
  border-radius:2px;
}
.content{
  margin-left: 16px;
  display:inline-block;
}
.title{
  margin:2px 0 8px 0;
}
.brand{
  vertical-align: top;
  display: inline-block;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
.name{
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.descreption{
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 12px;
}
.supports .icon{
  display: inline-block;
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
}
.supports .decrease{
  background-image: url("decrease_1@2x.png");
}
.supports .discount{
  background-image: url("discount_1@2x.png");
}
.supports .special{
  background-image: url("special_1@2x.png");
}
.supports .invoice{
  background-image: url("invoice_1@2x.png");
}
.supports .guarantee{
  background-image: url("guarantee_1@2x.png");
}
.supports .text{
  line-height: 12px;
  font-size: 12px;
}

.support-count{
  position:absolute;
  right: 12px;
  bottom: 14px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
  text-align:center;
}
.support-count .count{
  vertical-align: top;
  font-size: 10px;
}
.support-count .icon-keyboard_arrow_right{
  margin-left:2px;
  font-size: 10px;
  line-height: 24px;
}
.bulletin-wrapper{
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background:rgba(7,17,27,0.2);
}
.bulletin-wrapper .bulletin-title{
  display: inline-block;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
  margin-top: 8px;
}
.bulletin-wrapper .bulletin-text{
  margin: 0 4px; 
  font-size: 10px;
}
.bulletin-wrapper .icon-keyboard_arrow_right{
  position: absolute;
  font-size: 10px;
  right: 12px;
  top: 8px;
}
.background{
  position:absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter:blur(10px);
}

.detail{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: auto;
  transition: all 0.5s;
  backdrop-filter: blur(10px);
  background: rgba(7,17,27,0.8);
}
.detail:.fade-transition{
  opacity: 1;
  background: rgba(7,17,27,0.8);
}
.detail:.fade-enter,fade-leave{
  opacity: 0;
  background: rgba(7,17,27,0);
}
.detail-wrapper{
  min-height: 100%;
  width: 100%;
}
.detail-main{
  margin-top: 64px;
  padding-bottom: 64px;
}
.detail-main .name{
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.detail-main .star-wrapper{
  margin-top: 18px;
  padding: 2px 0;
  text-align:center;
}
.detail-main .title{
  width: 80%;
  margin: 28px auto 24px auto;
  display: flex;
}
.title .line{
  flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
.title .text{
  font-weight: 700;
  padding: 0 12px;
  font-size: 14px;
}
.supports{
  width: 80%;
  margin: 0 auto;
}
.support-item{
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.support-item:last-child{
  margin-bottom: 0;
}
.support-item .icon{
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  margin-right: 6px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.support-item .decrease{
  background-image: url("decrease_2@2x.png");
}
.support-item .discount{
  background-image: url("discount_2@2x.png");
}
.support-item .special{
  background-image: url("special_2@2x.png");
}
.support-item .invoice{
  background-image: url("invoice_2@2x.png");
}
.support-item .guarantee{
  background-image: url("guarantee_2@2x.png");
}
.support-item .text{
  line-height: 16px;
  font-size: 12px;
}
.detail-close{
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font=size:32px;
}
.bulletin{
  width: 80%;
  margin: 0 auto;
}
.bulletin .content{
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
  .brand{
    background-image: url("brand@3x.png");
  }
  .bulletin-wrapper .bulletin-title{
    background-image: url("bulletin@3x.png");
  }
  .supports .decrease{
  background-image: url("decrease_1@3x.png");
 }
.supports .discount{
  background-image: url("discount_1@3x.png");
 }
.supports .special{
  background-image: url("special_1@3x.png");
 }
.supports .invoice{
  background-image: url("invoice_1@3x.png");
 }
.supports .guarantee{
  background-image: url("guarantee_1@3x.png");
 }
 .support-item .decrease{
  background-image: url("decrease_2@3x.png");
}
.support-item .discount{
  background-image: url("discount_2@3x.png");
}
.support-item .special{
  background-image: url("special_2@3x.png");
}
.support-item .invoice{
  background-image: url("invoice_2@3x.png");
}
.support-item .guarantee{
  background-image: url("guarantee_2@3x.png");
}
}
</style>


