<template>
  <div class="star" :class="starType">
    <span v-for="v in itemClasses" :class="v" class="star-item"></span>
  </div>
</template>

<script>
const LENGTH = 5
const CLASS_ON = 'on'
const CLASS_HALF = 'half'
const CLASS_OFF = ' off'

export default {
  props: {
    size: {
      type: Number
    },
    score: {
      typr: Number
    }
  },
  computed: {
    starType () {
      return 'star-' + this.size
    },
    itemClasses () {
      var result = []
      var score = Math.floor(this.score * 2) / 2
      var hasDecimal = score % 1 !== 0
      var integer = Math.floor(score)
      for (var i = 0; i < integer; i++) {
        result.push(CLASS_ON)
      }
      if (hasDecimal) {
        result.push(CLASS_HALF)
      }
      while (result.length < LENGTH) {
        result.push(CLASS_OFF)
      }
      return result
    }
  },
  data () {
    return {

    }
  }
}
</script>

<style>
.star{

}
.star .star-item{
  display: inline-block;
  background-repeat: no-repeat;
}
.star-24 .star-item{
  width: 10px;
  height: 10px;
  margin-right: 3px;
  background-size: 10px 10px;
}
.star-24 .star-item:last-child{
  margin-right: 0;
}
.star-24 .on{
  background-image: url("star24_on@2x.png");
}
.star-24 .half{
  background-image: url("star24_half@2x.png");
}
.star-24 .off{
  background-image: url("star24_off@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
  .star-24 .on{
  background-image: url("star24_on@3x.png");
}
.star-24 .half{
  background-image: url("star24_half@3x.png");
}
.star-24 .off{
  background-image: url("star24_off@3x.png");
}
}

.star-36 .star-item{
  width: 15px;
  height: 15px;
  margin-right: 16px;
  background-size: 15px 15px;
}
.star-36 .star-item:last-child{
  margin-right: 0;
}
.star-36 .on{
  background-image: url("star36_on@2x.png");
}
.star-36 .half{
  background-image: url("star36_half@2x.png");
}
.star-36 .off{
  background-image: url("star36_off@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
  .star-36 .on{
  background-image: url("star36_on@3x.png");
}
.star-36 .half{
  background-image: url("star36_half@3x.png");
}
.star-36 .off{
  background-image: url("star36_off@3x.png");
}
}

.star-48 .star-item{
  width: 20px;
  height: 20px;
  margin-right: 22px;
  background-size: 20px 20px;
}
.star-48 .star-item:last-child{
  margin-right: 0;
}
.star-48 .on{
  background-image: url("star48_on@2x.png");
}
.star-48 .half{
  background-image: url("star48_half@2x.png");
}
.star-48 .off{
  background-image: url("star48_off@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3){
  .star-48 .on{
  background-image: url("star48_on@3x.png");
}
.star-48 .half{
  background-image: url("star48_half@3x.png");
}
.star-48 .off{
  background-image: url("star48_off@3x.png");
}
}
</style>


